/***** Global Styles *****/

/*** Box Sizing ***/
html {
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  font-size: 100%;
}
*, *:before, *:after {
  -moz-box-sizing: inherit;
       box-sizing: inherit;
}

/*** Body ***/
.wrap {
	background-color: #ffffff;
	color: #222222;
	line-height: 1.5;
	max-width: 1140px;
    margin: 0 auto;
    font-size: 18px;
}

header, section {
	margin-bottom: 3em;
}

small {
	font-size: 65%;
}

/*** Paragraph ***/
p {
	font-family: 'Noto Sans', Helvetica, sans-serif;
	margin: 0;
	font-size: 1em;
}
p + p {
	text-indent: 1em;
}

/*** Heading ***/
h1, h2, h3, h4, h5, h6 {
 font-family: 'Didact Gothic', Arial, Helvetica, sans-serif;
}
h1 {
	font-size: 2.986em;
}
h2 {
	font-size: 2.488em;
}
h3 {
	font-size: 2.074em;
}
h4 {
	font-size: 1.728em;
}
h5 {
	font-size: 1.44em;
}
h6 {
	font-size: 1.2em;
}

/*** List Styles ***/
ul {
	margin: 0;
	padding: 0;
}

li {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

/*** Link Styles ***/
a {
	text-decoration: none;
}
a:link {
  color: #ff6732;
}
a:visited {
  color: turquoise;
}
a:hover,
a:focus {
color: #ffa800; }
a:active {
  color: #ffa98b;
}

img {
	width: auto;
  	max-width: 100%;
  	height: auto;
}

/***** Mobile first styles go here *****/
/* Wrapper Div */
.wrap {
	width: 100%;
	max-width: 1140px;
    margin: 0 auto;
}

/* HEADER */
header {
	background-color: #eff7f9;
}

header h1 {
	text-align: center;
	border-bottom: 1px solid #c8bc9d;
	margin: 0;
	padding: 0;
}

header h1 a {
	display: block;
	width: 100%;
	color: #0f1314;
	padding: .3em 0;
 }

header h1 a:hover, a:focus {
	background-color: #ffffff;
}

header h1 a:active {
	background-color: #ffa98b;
}

nav li {
	margin: 0;
 	padding: 0;
 	text-align: center;
 	border-bottom: 1px solid #c8bc9d;
 }

nav a {
	display: block;
 	width: 100%;
 	text-transform: uppercase;
 	padding: .3em 0;
 	font-size: 1.2em;
 	letter-spacing: 2px;
 }

 nav a:hover, a:focus {
	background-color: #ffa800;
	color: #000000;
}

/* SECTION */
.avatar {
	margin: 0;
	padding: 0;
	margin-bottom: 2em;
}
.avatar div {
	width: 130px;
	height: 130px;
	margin: 0 auto;
}
span {
	color: blueviolet;
}

 .bio {
 	margin: 0;
 	padding: 0;
 }

.bio p {
	padding: 0 1em .5em 1em;
	text-align: center;
}

/* FOOTER */
footer {
	padding-bottom: 3em;
	text-align: center;
}

.social {
	margin: 0;
	margin-bottom: 3em;
	padding: 0;
	width: 100%;
}
.social p {
	text-align: center;
	margin-bottom: 1em;
}
.social ul {
	margin: 0;
	padding: 0;
}
.social li {
	margin: 0;
	padding: .5em 0;
	display: inline-block;
	width: 9.5em;
}
.social a {
	display: block;
	max-width: 20em; 
}

.social li:nth-child(1) {
	background-color: #a1ed00;
}
.social li:nth-child(2) {
	background-color: #e24e42;
}
.social li:nth-child(3) {
	background-color: #e98000;
}
.social li:nth-child(4) {
	background-color: #008f95;
}

/************** Desktop styles go here ***************/
/***** TAB ****/
@media screen and (min-width: 31.1111em) {
.wrap {
	width: 100%;
	font-size: 1.125em;
}

/* Row */
.row {
    margin: 0;
    padding: 0;
}	
  
/* Columns - Columns are created as needed. */
[class*="col"] {
    float: left;
    margin: 10px;
}

[class="col-1-2"] {
    width: -webkit-calc((100% - 20px * 2) / 2);
    width: calc((100% - 20px * 2) / 2);
}
}

/***** DESKTOP ****/
@media screen and (min-width: 44.4444em) {
.wrap {
	width: 100%;
	font-size: 1.18em;
}

/* Row */
.row {
    margin: 0;
    padding: 0;
}	
  
/* Columns - Columns are created as needed. */
[class*="col"] {
    float: left;
    margin: 10px;
}

[class="col-1-2"] {
    width: -webkit-calc((100% - 20px * 2) / 2);
    width: calc((100% - 20px * 2) / 2);
}
}
/* Clearfix */
.group:after {
  content: "";
  display: table;
  clear: both;
}